.documents {

    &-leave {
        opacity: 1;
    }

    &-enter {
        opacity: 0.01;
    }

    &-enter-active {
        opacity: 1;
        transition: opacity 500ms ease-in;
    }

    &-leave-active {
        opacity: 0.1;
        transition: opacity 300ms ease-in;
    }

}



